.timelineBox {
  opacity: 0;
  filter: alpha(opacity=0);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
  border-radius: 5px;
  background-color: #fff;
  display: flex;
  flex-direction: row-reverse;
  /* align-items: flex-end; */
  padding: 5px 10px;
  position: relative;
  width: max-content;
  max-width: 70%;
  /* text-align: right; */
  text-align: left;
  align-items: flex-start;
}

.timelineBox::after {
  content: " ";
  background-color: #fff;
  /* box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2); */
  position: absolute;
  /* right: -7.5px; */
  top: calc(50% - 7.5px);
  transform: rotate(45deg);
  width: 15px;
  height: 15px;
  right: auto;
  left: -7.5px;
  box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.2);
}

.timelineHalf {
  display: flex;
  /* max-width: 50%; */
  flex-wrap: wrap;
}

