.timelineBox .timelineTag {
  color: #fff;
  font-size: 12px;
  font-weight: bold;
  top: 5px;
  /* left: 5px; */
  letter-spacing: 1px;
  padding: 2px;
  position: relative;
  text-transform: uppercase;
  left: auto;
  right: 5px;
  /* justify-content: flex-end;
    width: auto; */
}

