}

.timelineContent .timelineCircle {
  background-color: #00b3bd;
  /* border: 3px solid #2aa6cf; */
  border: 3px solid var(--light1);
  border-radius: 50%;
  position: absolute;
  top: calc(50% - 10px);
  /* right: -40px; */
  width: 20px;
  height: 20px;
  z-index: 100;
  right: auto;
  left: -40px;
}

