@media only screen and (max-width: 1023px) {
  .timelineContent {
    max-width: 100%;
  }
  .project img {
    float: unset;
    max-width: unset;
    width: 100%;
    height: 100%;
    margin-left: unset;
    margin: 10px 0;
    /* object-fit: cover; */
  }
  .tags {
    display: inline;
    float: none;
    margin-left: 5px;
    padding: 1px 5px;
    border-radius: 5px;
  }
}

@media only screen and (max-width: 767px) {
  .timelineBox {
    display: none;
  }
  .timelineContent {
    width: 0px;
  }
  .timelineContainer {
    /* margin-left: 20px; */
